.i
{
    
  height: inherit;
  width:100%;
  background-color: #cf9FFF;
 
 
}
.i-left
{
    grid-template-rows: 1fr 1fr;
   position: inherit;
}
.i-right
{
    
    position: inherit;
}
.heading
{
    margin: 5px;
    font-size: 25px;
    font-weight: 300;
    text-decoration:underline;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    
}
.title
{
    margin: 10px;
}
.leftwrapper
{
    padding: 25px;
}
.titleItem
{
   
    font-size: 50px;
    font-weight:lighter;
    color:rgba(248, 246, 250, 0.925);
    display: flex;
    align-items: center;
    padding: 10px;
    margin: 10%;
    font-family: 'Lobster', cursive;

}
.Para
{
 
    color:rgba(248, 246, 250, 0.925);
    text-align: start;
  
   
    margin-left: 10%;
    margin-top: 20px;
    margin-bottom: 100px;
   
}
h1{
    color: white;
    font-family: 'Montserrat Alternates', sans-serif;

}
.i-img
{
    width: 70%;
    height: inherit;
    object-fit: contain;
    position:initial;
    margin: top 20px;;
    background-color: #d3d3d3;
    border-radius:450px ;
    margin: 25px;
    border-top: 10px;
    

}
.i-big
{
    clip-path: ellipse(25% 50% at 51% 74%);
    background-color: whitesmoke;
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    right:0;


}